import React from 'react';
import LocationModel from "../models/LocationModel";

interface State {
}

interface Props {
    locations:LocationModel[]
    onActionChange:(action:string) => void;
}

class ViewLocationList extends React.Component<Props,State> {
    constructor(props:Props) {
        super(props);
        this.state = {};
        this.onMapClick = this.onMapClick.bind(this)
    }

    onMapClick(e:any) {
        e.preventDefault();
        this.props.onActionChange('locations-map')
    }
    render() {
        return (
            <div className="container locations">
                <div className="row">
                    <div className="col-12">
                    <ul className="list-group mt-3">
                        {this.props.locations.map((item, index) => (
                            <li className="list-group-item location" key={index}>
                                <h3>{item.name}</h3>
                                 <span className="badge badge-info">{item.category}</span>
                                 <span className="badge badge-primary pull-right">{item.distance.toFixed(2)}km</span>
                            </li>
                        ))}
                    </ul>
                    </div>
                </div>

                <div className="buttons-left mr-3 ml-3">
                    <button type="button" className="btn btn-primary" onClick={this.onMapClick} >
                        Mapa
                    </button>
                </div>
            </div>
        )
    }
}
export default ViewLocationList;
