import React from 'react';
import LocationModel from "../models/LocationModel";
import { Map, Marker, TileLayer } from "react-leaflet";

import "./ViewLocationMap.css"

interface State {
    locationIndex:number,
}

interface Props {
    locations:LocationModel[]
    onActionChange:(action:string) => void;
    latitude:number,
    longitude:number
}

class ViewLocationMap extends React.Component<Props,State> {
    mapRef:any;
    map:any;
    constructor(props:Props) {
        super(props);
        this.state = {
            locationIndex:-1
        };

        this.mapRef = React.createRef();
        this.onDetailsCloseClick = this.onDetailsCloseClick.bind(this)
        this.onListClick = this.onListClick.bind(this)
        this.mapCenterClick = this.mapCenterClick.bind(this)
    }

    componentDidMount() {
        this.map = this.mapRef.current.leafletElement;
    }

    onDetailsCloseClick(e:any) {
        e.preventDefault();
        this.setState({locationIndex:-1})
    }

    onListClick(e:any) {
        e.preventDefault();
        this.props.onActionChange('locations-list')
    }
    mapCenterClick(e:any){
       this.map.panTo([this.props.latitude,this.props.longitude]);
    }

    render() {
        let item:any = this.state.locationIndex >-1?this.props.locations[this.state.locationIndex]:{};
        return (
            <div className="container locations">
                {this.state.locationIndex >-1 &&  <div className="location-details">
                    <i className="fa fa-close float-right p-2" onClick={this.onDetailsCloseClick} />
                    <h3>{item.name}</h3>
                    <span className="badge badge-info">{item.category}</span>
                    <span className="badge badge-primary pull-right">{item.distance.toFixed(2)}km</span>

                </div>}
                <Map center={[this.props.latitude,this.props.longitude]} zoom={12} maxZoom={17} minZoom={8} ref={this.mapRef}>
                    <TileLayer
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    />
                    {this.props.locations.map((item, index) => (
                        <Marker
                            key={index}
                            position={[
                                item.latitude,
                                item.longitude
                            ]}
                            onClick={() => {
                                this.setState({locationIndex:index})
                            }}
                        />
                    ))}
                </Map>
                <div className="buttons-left mr-3 ml-3">
                    <button type="button" className="btn btn-primary" onClick={this.onListClick} >
                        Lista
                    </button>
                </div>
                <div className="buttons-right mr-3 ml-3">
                    <button type="button" className="btn btn-primary" onClick={this.mapCenterClick} >
                        <i className="fa fa-crosshairs"></i>
                    </button>
                </div>
            </div>
        )
    }
}
export default ViewLocationMap;
