import React from 'react';
import ViewHome from "./ViewHome";
import Helpers from "../common/Helpers"
import ViewLocationList from "./ViewLocationList"
import ViewLocationMap from "./ViewLocationMap"
import DC from "../common/DataConector";
import LocationModel from "../models/LocationModel"

interface State {
  isLoading: boolean,
  menuShow: boolean,
  viewCode:string,
  locations:LocationModel[],
  latitude: number,
  longitude: number,
}

interface Props {
}

class App extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isLoading:true,
      menuShow:false,
      viewCode:'home',
      locations:[],
      latitude:-24.6728445,
      longitude:-56.706004
    }

    this.pageLoading = this.pageLoading.bind(this)
    this.onHomeClick = this.onHomeClick.bind(this)
    this.onActionChange = this.onActionChange.bind(this)
    this.locationSort = this.locationSort.bind(this)
  }

  async componentDidMount() {

    let latitude = -24.6728445;
    let longitude = -56.706004;

    let base = this;
    await navigator.geolocation.getCurrentPosition(function (position){
          latitude= position.coords.latitude;
          longitude= position.coords.longitude;
          base.setState({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          })
          base.locationSort(latitude,longitude);
        },
        function (err) {
          base.locationSort(latitude,longitude);
        }
    );

  }

 async locationSort(latitude:number,longitude:number){

   const getData = {url: '/gvpysalud/data/locations.json'}
    let content = await DC.Get(getData);
    for (let x = 0; x < content.length; x++) {
      content[x].distance = Helpers.Distance(latitude, longitude, content[x].latitude, content[x].longitude)
    }
    content.sort((a:any,b:any) => (a.distance > b.distance) ? 1 : ((b.distance > a.distance) ? -1 : 0));
    this.setState({locations: content, isLoading: false})
  }

  pageLoading(active: boolean) {
    this.setState({isLoading: active})
  }

  onHomeClick(e:any) {
    e.preventDefault();
    this.setState({viewCode:'home'})
  }

  onActionChange(action:string) {
    this.setState({viewCode:action})
  }

  renderMain() {
    switch (this.state.viewCode) {
      case "home":
        return <ViewHome pageLoding={this.pageLoading} onActionChange={this.onActionChange}/>
      case "locations-list":
        return <ViewLocationList locations={this.state.locations} onActionChange={this.onActionChange} />
      case "locations-map":
        return <ViewLocationMap locations={this.state.locations} onActionChange={this.onActionChange}
          latitude={this.state.latitude} longitude={this.state.longitude}/>
      default:
        return null;
    }
  }
  render() {
    return (
        <div className="container">
          <header className="blog-header py-3">
            <div className="row flex-nowrap justify-content-between align-items-center">
              <div className="col-4 pt-1 pointer" onClick={this.onHomeClick}>
                {this.state.viewCode !== 'home' && <i className="fa fa-home"/> }
              </div>
              <div className="col-4 text-center pointer" onClick={this.onHomeClick}>
                 Map Viewer
              </div>
              <div className="col-4 d-flex justify-content-end align-items-center">
              </div>
            </div>
          </header>
          {/*   <Menu menuHide={this.onMenuClick} menuShow={this.state.menuShow}/>*/}

          <main role="main" className="container">
              {this.renderMain()}
            <br/>
          </main>
          <footer>
            Desarrollado por <a href="https://geovoluntarios.org" target="_blank"> <img src="/gvpysalud/images/logo192.png"/></a>
          </footer>
        </div>
    )
  }
}

export default App;
