import React from 'react';
import './ViewHome.css';
import DC from "../common/DataConector";

interface HomeItem{
    type:string,
    text:string,
    action: string
}

interface State {
    items:HomeItem[]
}

interface Props {
    pageLoding:(loading:boolean) => void;
    onActionChange:(action:string) => void;
}

class ViewHome extends React.Component<Props,State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            items: []
        };
        this.onActionClick = this.onActionClick.bind(this)
    }

    async componentDidMount() {
        this.props.pageLoding(true);
        if (this.state.items.length === 0) {
            const getData = {url: '/gvpysalud/data/home.json'}
            let content = await DC.Get(getData);
            this.setState({items: content});
        }
        this.props.pageLoding(false);
    }

    onActionClick(e:any) {
        e.preventDefault();
        this.props.onActionChange(e.currentTarget.dataset.code);
    }

    renderType(item: any, index: number) {
        switch (item.type) {
            case 'h1':
                return <h1 className="text-center">{item.text}</h1>
            case 'h2':
                return <h2 className="text-center">{item.text}</h2>
            case 'p':
                return <p>{item.text}</p>
            case 'btn':
                return <p><button className="btn btn-primary" onClick={this.onActionClick} data-code={item.action}>{item.text}</button></p>
            default:
                return null;
        }
    }

    render() {
        return (
            <div>
                <div className="row mt-3">
                    {this.state.items.map((item, index) => (
                        <div className="col-12" key={index}>
                            {this.renderType(item, index)}
                        </div>
                    ))}
                </div>
                <div>
                    <br/>
                </div>
            </div>
        );
    }
}

export default ViewHome;
