import axios from 'axios';
import Helpers from "./Helpers"
const qs = require("qs");

class DC {
    static header() {
        return {};
    }
    public static async Post(postData: any) {
        const url = postData.url;
        delete postData.url;
        const response = await axios({
            method: 'post',
            url: url,
            data: qs.stringify(postData),
            headers: this.header()
        }).catch(error => {
            console.log(error);
        });
        if (response !== undefined) return response.data; else return {wasOk: false};
    }

    public static async PostForm(postData: any) {
        const data = new FormData(postData.form.current);
        const response = await axios({
            method: 'post',
            url: postData.url,
            data: data,
            headers: {'Content-Type': 'multipart/form-data'}
        }).catch(error => {
            console.log(error);
        });
        if (response !== undefined) return response.data; else return {wasOk: false};
    }

    public static async Get(getData: any) {
        var url = getData.url;
        delete getData.url;
        url = url + '?' + Helpers.ObjectToQueryString(getData);

        const response = await axios({
            method: 'get',
            url: url,
            headers: this.header()
        }).catch(error => {
            console.log(error);
        });

        if (response !== undefined) return response.data; else return {wasOk: false};
    }
}

export default DC;